.Conversation {
  display: flex;
  align-items: center;
  padding: 10px;

  margin-top: 20px;

  &:hover {
    background-color: gainsboro;
  }
  .Conversation_Img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;

    cursor: pointer;
  }
  .Conversation_Name {
    font-weight: 500;
  }
  .Converstaion_div {
    display: flex;
    flex-direction: column;
  }
  .Conversation_short {
    // width: 80px;
    width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
